/* CSS Document */
/*
** HTML5移动重置样式表
*/
/* ============================ 重置css样式 ============================ */
/* 清除内外边距 */
body,html,header,footer,section,span,p,h1,h2,h3,h4,ul,ol,li,select,input,button,textarea,aside,article,dd,dl,dt{
  margin: 0;
  padding: 0;
}
/* 设置默认字体 */
body{
	font:0.6rem "\5FAE\8F6F\96C5\9ED1", Helvetica, Arial, sans-serif;
	color: #3d3d3d;
	 }
/* 重置列表元素 */
ul, ol { list-style: none; }
/* 重置文本格式元素 */
a { text-decoration: none; }
a:focus { outline:none; }
img{
  width:100%;
  border:none;
  vertical-align:middle;
  }
/* 重置表格元素 */
/* ========================= 页面常用样式 CSS样式 =========================== */
html,body{
  width:100%;
  overflow-x:hidden;
}
body{
  -text-size-adjust:none;
  overflow-x:hidden;
  -webkit-user-select:none;
  -moz-user-select:none;
   -ms-user-select:none;
       user-select:none;
  -webkit-tap-highlight-color:rgba(0,0,0,0);
}
/*清除浮动*/
.clearfix:after{
  display:block;
  clear:both;
  height:0;
  visibility:hidden;
  content:" ";
}
h1,h2,h3,h4,h5,h6{font-weight:normal;}
.fl{float:left;}
.fr{float:right;}
input{ border: none; border-radius: 0px; background:none;outline:none;}
input[type="button"],input[type="submit"],input[type="reset"]{
  -webkit-appearance:none;
  -moz-appearance:none;
       appearance:none;
}
select,button{ -webkit-appearance: none; border:none;}
i,em{ font-style:normal;}
/*placeholder文字默认颜色-webkit-input-placeholder*/
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ color: #bfbfbf;}
::-moz-placeholder { /* Mozilla Firefox 19+ */ color: #bfbfbf;}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {  color: #bfbfbf;}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {  color: #bfbfbf;}

